<template>
  <section class="portfolio container" id="Portfolio">
    <div class="portfolio__content">
      <div class="title">
        <h3 class="title__h3">{{ $t('recentWorks') }}</h3>
        <p class="title__p">{{ $t('myPortfolio') }}</p>
      </div>
      <!-- <div class="portfolio__tab">
        <ul class="portfolio__tab-link">
          <li>
            <button type="button" class="portfolio__tab-web button">
              {{ $t('web') }}
            </button>
          </li>
        </ul>
      </div> -->
      <div class="portfolio__sectoion">
        <div class="portfolio__web" @click="portfolioActive = false">
          <div
            class="portfolio__item"
            :class="{ left: card.to, right: !card.to }"
            v-for="(card, i) in portfolio"
            :key="card.id"
            @click="(portfolioActive = !portfolioActive), addActive(i)"
          >
            <h3 class="portfolio__item-h3">{{ card.title }}</h3>
            <img :src="card.path" alt="" class="portfolio__item-img" />
            <a
              :href="card.link"
              target="_blank"
              class="button portfolio__item-btn"
              >{{ $t('viewMore') }}</a
            >
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    portfolio: Array,
  },
  data() {
    return {
      portfolioActive: false,
    };
  },
  methods: {
    addActive(i) {
      let arr = document.querySelectorAll(".portfolio__item");

      arr.forEach((el) => {
        el.classList.remove("active");
      });
      if (this.portfolioActive) {
        arr[i].classList.add("active");
      }
    },
  },
};
</script>

