import { createI18n } from 'vue-i18n'
import ru from '@/i18n/locales/ru.json'
import en from '@/i18n/locales/en.json'

// 2. Создать экземпляр i18n с параметрами

export default createI18n({
    legacy: true, // вы должны установить `false`, чтобы использовать Composition API
    locale: 'en', // установить локаль
    fallbackLocale: 'ru', // установить резервную локаль
    globalInjection: true,
    messages: { ru, en } // установить локальные сообщения
    // Если вам нужно указать другие параметры, вы можете установить другие параметры
    // ...
})