<template>
  <header class="header">
    <nav class="header__nav">
      <div class="container">
        <a href="/" class="header__logo">{{ $t('logo') }}</a>
        <button
          class="menu"
          :class="{ active: menu }"
          @click="(menu = !menu), (icon = false)"
        >
          <span></span>
          <span></span>
          <span></span>
        </button>
        <span
          @click="(icon = false), (menu = false)"
          class="iconClose"
          :class="{ width: icon }"
        ></span>
        <ul class="header__list">
          <li v-for="(link, i) in links" :key="i">
            <a
              :href="`#${link.name}`"
              class="header__link"
              :class="{ active: link.isActive }"
              @click="addActive(links, link)"
              >{{ $t(link.name) }}</a
            >
          </li>
        </ul>

        <button
          class="header__btn"
          :class="{ active: icon }"
          @click="(icon = !icon), (menu = false)"
        >
          <fa icon="sliders" class="header__icon" />
        </button>
        <div class="header__dropdown">
          <button
            class="header__dropdown-en"
            :class="{ active: this.$i18n.locale == 'en' }"
            @click="clickedEn(this.$i18n.locale)"
          >
            EN
          </button>
          <button
            class="header__dropdown-ru"
            :class="{ active: this.$i18n.locale == 'ru' }"
            @click="clickedRu(this.$i18n.locale)"
          >
            RU
          </button>
          <span
            class="header__dropdown-span"
            :class="{ active: this.$i18n.locale == 'en'}"
          ></span>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
export default {
  props: ["links", "icon", "menu"],
  data() {
    return {
    }
  },
  created() {
    this.$i18n.locale = localStorage.lang || 'en'
  },
  methods: {
    addActive(links, link) {
      links.forEach((item) => {
        item.isActive = false;
      });
      link.isActive = true;
    },
    clickedEn() {
      this.$i18n.locale = "en"
      localStorage.lang = 'en';
    },
    clickedRu() {
      this.$i18n.locale = "ru"
      localStorage.lang = 'ru';
    },
  },
};
</script>

