<template>
  <section class="experience container" id="Experience">
    <div class="experience__content">
      <div class="title">
        <h3 class="title__h3">{{ $t('myExperience') }}</h3>
        <p class="title__p">{{ $t('myAbilities') }}</p>
      </div>
      <div class="experience__progressbar">
        <div
          class="experience__card"
          v-for="card in exp"
          :key="card.id"
          :class="{ active: viewColor }"
        >
          <Transition name="experience__progress">
            <div
              class="experience__progress"
              :class="{ active: viewColor }"
              :style="{ '--i': card.percent }"
              v-show="viewE"
            >
              <h3 class="experience__progress-percent" v-if="viewColor">
                {{ card.percent }}<span>%</span>
              </h3>
              <h4 class="experience__progress-title" v-if="viewColor">
                {{ card.title }}
              </h4>
              <span class="experience__dot"></span>
            </div>
          </Transition>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ["exp"],
  created() {
    this.viewExp();
  },
  data() {
    return {
      viewE: false,
      viewColor: false,
      e: null,
    };
  },
  methods: {
    viewExp() {
      window.addEventListener("scroll", () => {
        if (window.pageYOffset >= 1260 && window.pageYOffset <= 1980) {
          this.viewE = true;
          setTimeout(() => {
            this.viewColor = true;
          }, 1500);
        }
      });
    },
  },
};
</script>

